namespace $ {
	export class $hyoo_crowds_node_page extends $mol_page {
		
		/**
		 * ```tree
		 * node $hyoo_crowds_node
		 * ```
		 */
		@ $mol_mem
		node() {
			const obj = new this.$.$hyoo_crowds_node()
			
			return obj
		}
		
		/**
		 * ```tree
		 * title \Node
		 * ```
		 */
		title() {
			return "Node"
		}
		
		/**
		 * ```tree
		 * body /
		 * 	<= Text
		 * 	<= Dump
		 * ```
		 */
		body() {
			return [
				this.Text(),
				this.Dump()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * text? \
		 * ```
		 */
		@ $mol_mem
		text(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * Text $mol_textarea value? <=> text?
		 * ```
		 */
		@ $mol_mem
		Text() {
			const obj = new this.$.$mol_textarea()
			
			obj.value = (next?: any) => this.text(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Dump $hyoo_crowds_node_dump node <= node
		 * ```
		 */
		@ $mol_mem
		Dump() {
			const obj = new this.$.$hyoo_crowds_node_dump()
			
			obj.node = () => this.node()
			
			return obj
		}
	}
	
}

