namespace $ {
	export class $hyoo_crowds_expose extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \CROWDS Expose
		 * ```
		 */
		menu_title() {
			return "CROWDS Expose"
		}
		
		/**
		 * ```tree
		 * param \section
		 * ```
		 */
		param() {
			return "section"
		}
		
		/**
		 * ```tree
		 * realm $hyoo_crowds_realm
		 * ```
		 */
		@ $mol_mem
		realm() {
			const obj = new this.$.$hyoo_crowds_realm()
			
			return obj
		}
		
		/**
		 * ```tree
		 * spreads * realm <= Realm
		 * ```
		 */
		spreads() {
			return {
				realm: this.Realm()
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * Placeholder null
		 * ```
		 */
		Placeholder() {
			return null as any
		}
		
		/**
		 * ```tree
		 * Realm $hyoo_crowds_realm_book realm <= realm
		 * ```
		 */
		@ $mol_mem
		Realm() {
			const obj = new this.$.$hyoo_crowds_realm_book()
			
			obj.realm = () => this.realm()
			
			return obj
		}
	}
	
}

