namespace $ {
	export class $hyoo_crowds_area_book extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \Heads
		 * ```
		 */
		menu_title() {
			return "Heads"
		}
		
		/**
		 * ```tree
		 * param \node
		 * ```
		 */
		param() {
			return "node"
		}
		
		/**
		 * ```tree
		 * area $hyoo_crowds_area
		 * ```
		 */
		@ $mol_mem
		area() {
			const obj = new this.$.$hyoo_crowds_area()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Spread* <= Node*
		 * ```
		 */
		Spread(id: any) {
			return this.Node(id)
		}
		
		/**
		 * ```tree
		 * node* $hyoo_crowds_node
		 * ```
		 */
		@ $mol_mem_key
		node(id: any) {
			const obj = new this.$.$hyoo_crowds_node()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Node* $hyoo_crowds_node_page node <= node*
		 * ```
		 */
		@ $mol_mem_key
		Node(id: any) {
			const obj = new this.$.$hyoo_crowds_node_page()
			
			obj.node = () => this.node(id)
			
			return obj
		}
	}
	
}

