namespace $ {
	export class $hyoo_crowds_land_book extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \Areas
		 * ```
		 */
		menu_title() {
			return "Areas"
		}
		
		/**
		 * ```tree
		 * param \area
		 * ```
		 */
		param() {
			return "area"
		}
		
		/**
		 * ```tree
		 * land $hyoo_crowds_land
		 * ```
		 */
		@ $mol_mem
		land() {
			const obj = new this.$.$hyoo_crowds_land()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Spread* <= Area*
		 * ```
		 */
		Spread(id: any) {
			return this.Area(id)
		}
		
		/**
		 * ```tree
		 * menu_tools / <= Area_new
		 * ```
		 */
		menu_tools() {
			return [
				this.Area_new()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * area* $hyoo_crowds_area
		 * ```
		 */
		@ $mol_mem_key
		area(id: any) {
			const obj = new this.$.$hyoo_crowds_area()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Area* $hyoo_crowds_area_book area <= area*
		 * ```
		 */
		@ $mol_mem_key
		Area(id: any) {
			const obj = new this.$.$hyoo_crowds_area_book()
			
			obj.area = () => this.area(id)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Area_new_icon $mol_icon_plus
		 * ```
		 */
		@ $mol_mem
		Area_new_icon() {
			const obj = new this.$.$mol_icon_plus()
			
			return obj
		}
		
		/**
		 * ```tree
		 * area_new? null
		 * ```
		 */
		@ $mol_mem
		area_new(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Area_new $mol_button_minor
		 * 	sub / <= Area_new_icon
		 * 	click? <=> area_new?
		 * ```
		 */
		@ $mol_mem
		Area_new() {
			const obj = new this.$.$mol_button_minor()
			
			obj.sub = () => [
				this.Area_new_icon()
			] as readonly any[]
			obj.click = (next?: any) => this.area_new(next)
			
			return obj
		}
	}
	
}

