namespace $ {
	export class $hyoo_crowds_realm_book extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \Lands
		 * ```
		 */
		menu_title() {
			return "Lands"
		}
		
		/**
		 * ```tree
		 * param \land
		 * ```
		 */
		param() {
			return "land"
		}
		
		/**
		 * ```tree
		 * realm $hyoo_crowds_realm
		 * ```
		 */
		@ $mol_mem
		realm() {
			const obj = new this.$.$hyoo_crowds_realm()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Spread* <= Land*
		 * ```
		 */
		Spread(id: any) {
			return this.Land(id)
		}
		
		/**
		 * ```tree
		 * land* $hyoo_crowds_land
		 * ```
		 */
		@ $mol_mem_key
		land(id: any) {
			const obj = new this.$.$hyoo_crowds_land()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Land* $hyoo_crowds_land_book land <= land*
		 * ```
		 */
		@ $mol_mem_key
		Land(id: any) {
			const obj = new this.$.$hyoo_crowds_land_book()
			
			obj.land = () => this.land(id)
			
			return obj
		}
	}
	
}

